<template>
  <div class="common szui">
    <div class="top">
      <div class="logo">
        <img src="./assets/szui.png" alt />
        <img src="./assets/szuiz.png" alt class="zi" />
      </div>
    </div>
    <div class="left">
      <div class="left-item">
        <div :class="{'active':currName == item.key}" v-for="(item,i) in btns" :key="i">
          <a @click="goCom(item.key)">{{item.name}}</a>
        </div>
      </div>
    </div>
    <div class="right">
      <div class="zujianitem" ref="aa" name="sz-Sweiper">
        <Sweiper />
      </div>
      <div class="zujianitem" ref="bb" name="sz-Slide">
        <Slide />
      </div>
      <div class="zujianitem" ref="cc" name="sz-Sweiper">
        <Sweiper />
      </div>
    </div>
  </div>
</template>

<script>
import Sweiper from "./views/Sweiper";
import Slide from "./views/Slide";
export default {
  components: {
    Sweiper,
    Slide
  },
  data() {
    return {
      currName: "aa",
      pathArr: [],
      btns: []
    };
  },
  methods: {
    goCom(key) {
      this.currName = key;
      // 去对应的位置
      document.documentElement.scrollTop = this.$refs[key].offsetTop - 94;
    }
  },
  created() {
    
  },
  mounted() {
    for (const key in this.$refs) {
      let btnObj = {
        key: key,
        name: this.$refs[key].getAttribute("name")
      };
      this.btns.push(btnObj);
    }
    window.onscroll = () => {
      for (const key in this.$refs) {
        let top = this.$refs[key].offsetTop -600;
        let winTop = document.documentElement.scrollTop;
        if (top < winTop) this.currName = key;
      }
    };
  }
};
</script>
